;(function(){
  angular.module('triflix')
  .component('multiplayer', {
    bindings: {
    },
    templateUrl: 'app/components/panels/multiplayer/multiplayer.html',
    controller: multiplayerCtrl,
    controllerAs: 'multiplayer'
  });

  multiplayerCtrl.$inject = ['$scope', 'ApiPath', 'UserService', '$uibModal', 'PanelService'
  , 'SocketService', '$rootScope', 'PanelService'];

  function multiplayerCtrl($scope, ApiPath, UserService, $uibModal, PanelService,
    SocketService, $rootScope, PanelService){

    var self = this;
    self.user = UserService.getUser();
    var user = self.user;
    if(!user) return $scope.$parent.$close(); //display you have to login to use this feature
    if(!user.facebook) return $scope.$parent.$close();
    //gestire timeout per connessione assente

    SocketService.initSocket();

    var openBoard = function(){
      PanelService.open({
        component: 'board',
        scope: $scope
      });
    }

    self.chooseOpponent = function(opponent){
      if(self.users){
        var opponentSocketId = SocketService.getOpponentSocketFromValue(opponent, self.users);
        SocketService.emit('choose opponent', opponentSocketId);
      }
    }

    SocketService.emit('add to room', {
      userId: user.facebook.id,
      data: user
    });


    SocketService.on('add to room', function(data){
      $scope.$apply(function(){
        self.users = data.users;
        self.usersOnline = _.map(data.users, function(value, key){
          return value;
        })
        //self.usersOnline = [self.usersOnline[0], self.usersOnline[0], self.usersOnline[0], self.usersOnline[0]];
      })
    });

    SocketService.on('challenge request', function(opponent){
      var text = 'Challenge request from '+ opponent.opponent.data.facebook.name;
      $uibModal.open({
        animation: true,
        component: 'confirm',
        backdrop: 'static',
        resolve: {
          text: [function(){
            return text;
          }],
          okCb: [function(){
            return function(){
              SocketService.emit('challenge accepted', {
                accepter: SocketService.getSocketId(),
                challenger: opponent.opponentSocketId
              })
              SocketService.chooseOpponent(opponent);
              $rootScope.$emit('triflix.game.start');
              openBoard();
            }
          }]
        }
      });
    });

    SocketService.on('challenge accepted', function(opponent){
      SocketService.chooseOpponent(opponent);
      $rootScope.$emit('triflix.game.start');
      openBoard();
    })

    this.openProfile = function(userSelected){
      PanelService.open({
        component: 'profile',
        scope: $scope,
        resolve: {
          user: function(){
            return userSelected;
          }
        }
      });
    }
    this.params = {
      back: {
        text: 'Back',
        cb: function(){
          SocketService.removeOpponent();
          $scope.$parent.$close();
        }
      }
    };


  }
})();

;(function(){
  angular.module('triflix')
  .component('login', {
    bindings: {
    },
    templateUrl: 'app/components/panels/login/login.html',
    controller: loginCtrl,
    controllerAs: 'login'
  });

  loginCtrl.$inject = ['$scope', 'UserService', '$timeout', 'PanelService', 'TEAMS',
  'ANIMATIONS', 'ApiPath'];

  function loginCtrl($scope, UserService, $timeout, PanelService, TEAMS, ANIMATIONS,
  ApiPath){
    checkIncognitoMode();
    var isIncognitoMode = false;
    this.teams = TEAMS;
    this.animations = ANIMATIONS;
    var openPanel= function(panel){
      PanelService.open({
        component: panel,
        scope: $scope
      });
    }

    var getUser = function(panel){
      UserService.user()
      .then(function(resp){
        this.user = resp.data;
        if(panel)
          $timeout(function(){
            openPanel(panel);
          }, 100);
      }, function(err){
        console.log(err);
      })
    }
    getUser('');

    this.continueNotLogged = function(){
      openPanel('board');
    }

    this.loginWithFacebook = function(){
      if(window.localStorage.getItem('x-auth'))
        getUser('multiplayer');
      else
        facebookAuth();
    }

    this.openSettings = function(){
      openPanel('settings');
    }

    var facebookAuth = function(){
      goTo(ApiPath.login.remote);
    }

    /*workaround per fare settare al browser l'header referer fondamentale al login
    a node per fare il redirect corretto. con window.location non viene settato l'header referer*/
    function goTo(url){
      var a = document.createElement("a");
      if(!a.click) //for IE
      {
           window.location = url;
           return;
      }
      a.setAttribute("href", url);
      a.style.display = "none";
      document.body.appendChild(a);
      a.click();
    }

    /*utile perchè in modalità incognito non funziona il localstorage*/
    function checkIncognitoMode(){
      var fs = window.RequestFileSystem || window.webkitRequestFileSystem;
      if (!fs) {
        console.log('check failed');
        isIncognitoMode = false;
      } else{
        fs(window.TEMPORARY,
           100,
           function(){
             isIncognitoMode = false; //not in incognito
           },
           function(){
             isIncognitoMode = true; //incognito mode
             getUser('multiplayer');
           });
      }
    }
  }
})();

