;(function(){
  angular.module('triflix')
  .component('triflixBoard', {
    bindings: {
      index: '@'
    },
    templateUrl: 'app/components/triflix-board/triflix-board.html',
    controller: triflixBoardCtrl,
    controllerAs: 'board'
  });

  triflixBoardCtrl.$inject = ['$element', '$compile', '$scope', 'Game',
  '$rootScope', '$uibModal', 'TEAMS', 'SocketService', 'ScoreService'];

  function triflixBoardCtrl($element, $compile, $scope, Game, $rootScope, $uibModal,
    TEAMS, SocketService, ScoreService){
    var game, self = this;

    this.$onInit = function(){
      self.userMove = true;
      Game.lockBoard = true;
      Game.reset();
      game = Game.getStatus();
      this.game = game;
      if(SocketService.getOpponent()){
        self.opponentName = SocketService.getOpponent().opponent.data.facebook.name.split(" ")[0];
        Game.lockBoard = false;
        SocketService.on('make move', function(opponentMove){
          $scope.$apply(function(){
            game.state[opponentMove] = game.team === TEAMS.X ? TEAMS.O : TEAMS.X;
            self.userMove = true;
            if(Game.winner(game)){
              SocketService.emit('winner or draw', {
                description: "test",
                player1: user.facebook.id,
                player2: SocketService.getOpponent().opponent.data.facebook.id,
                winner: game.winner.team,
                state: game.state
              })
              openModal('victoryModal', function(dismissed){
                Game.reset();
                $rootScope.$emit('triflix.game.start');
              });
            }
          })
        });
        SocketService.on('game aborted', function(){
          $scope.$parent.$parent.$close();
        })
      }
    }

    this.makeMove = function(x, y){
      var flatCoordinate = Game.flatCoordinate(x, y);
      if(Game.lockBoard){
        return;
      }
      if(game.state[flatCoordinate] === TEAMS.EMPTY && self.userMove){
        self.userMove = false;
        game.state[flatCoordinate] = game.team;
        Game.opponentMove(game, flatCoordinate)
        .then(function(resp){
          _.extend(game, resp.data);
          if(!SocketService.getOpponent()){
            self.userMove = true;
          }

          if(game.winner.team){
            $rootScope.$emit('triflix.game.victory');
            openModal('victoryModal', function(dismissed){
              Game.reset();
              $rootScope.$emit('triflix.game.start');
            });
          }

        })
        .catch(function(error){
          if(error.message === 'draw'){
            openModal('victoryModal', function(dismissed){
              Game.reset();
              $rootScope.$emit('triflix.game.start');
              self.userMove = true;
            });
          } else{
            console.log(error);
          }
        })
      }
    }


    this.$postLink = function(){}

    var openModal = function(component, callback){
      var modal = $uibModal.open({
        animation: true,
        component: component,
        resolve: {
          game: [function(){
            return self.game;
          }]
        }
      })
      modal.result.catch(callback)
    }

    var hasEmptySlots = function(state){
      var foundOne = _.find(state, function(cell){return cell === TEAMS.EMPTY});
      return foundOne !== undefined ? true : false;
    }
  }
})();

;(function(){
  angular.module('triflix')
  .component('panelContainer', {
    bindings: {
    },
    controller: panelContainerCtrl,
    controllerAs: 'panel'
  });

  panelContainerCtrl.$inject = ['$element', '$scope', '$rootScope', '$templateCache', '$compile',
   '$window', '$timeout'];

  function panelContainerCtrl($element, $scope, $rootScope, $templateCache, $compile,
    $window, $timeout){


      var width, height, leftPos;

      function initWindow(){
        width = window.innerWidth
        || document.documentElement.clientWidth
        || document.body.clientWidth;

        height = window.innerHeight
        || document.documentElement.clientHeight
        || document.body.clientHeight;
      }
      initWindow();

      this.$onInit = function(){
        $element.css('position', 'absolute');
        $element.css('width', width);
        $element.css('height', height);
        $element.css('top', '0px');
        $element.css('left', -width + 'px');
      }

      var panels = [];
      $rootScope.$on('triflix.addpanel', function(evt, params){
        var newScope = $rootScope.$new();
        var elem = angular.element('<div></div>');
        elem.css('float', 'left');
        elem.css('width', width);
        elem.css('height', height);

        if(params.controller){
          var ctrl = angular.element('<div></div>');
          ctrl.attr('ng-controller', params.controller);
          var template = $templateCache.get(params.templateUrl);
          ctrl.append(angular.element(template));
          elem.append(ctrl);
        }
        else if(params.component){
          var component = '<' + params.component;
          if(params.resolve && typeof(params.resolve) === "object"){
            for(var key in params.resolve){
              newScope['resolved' + key] = params.resolve[key]();
              component += ' ' + key + '="resolved' + key + '"';
            }
          }
          component += '></' + params.component + '>';
          elem.append(component);
        }


        newScope.prototype = params.scope;
        newScope.$close = function(){
          var index = panels.indexOf(panel);
          panels.splice(index, 1);
          panel.scope().$destroy();
          var leftPos = panels.length ? -width * (panels.length - 1) : 0;

          $element.css('left', leftPos);
          $timeout(function(){
            panel.remove();
            $element.css('width', width * panels.length);
          }, 500);

        }
        var panel = $compile(elem)(newScope);
        panels.push(panel);

        $element.parent().css('position', 'relative');
        $element.parent().css('overflow-x', 'hidden');
        $element.parent().css('width', width);
        $element.parent().css('height', height);

        $element.css('width', width * panels.length);
        $element.append(panel);
        var leftPos = -width * (panels.length - 1);

        $element.css('left', leftPos);

      });

      this.$postLink = function(){
        angular.element($window).on('resize', recalculateSize);
      }

      function recalculateSize(){
        initWindow();
        panels.forEach(function(panel){
          panel.css('height', height);
          panel.css('width', width);
        })

        $element.css('height', height);
        $element.css('width', width * panels.length);
        $element.css('left', (- width * (panels.length - 1)));
        $element.parent().css('height', height);
        $element.parent().css('width', width);
      }
  }
})();

;(function(){
  angular.module('triflix')
  .directive('svgIcon', svgIconDirective)
  .service('svgIconService', svgIconService);

  svgIconDirective.$inject = ['svgIconService', 'ANIMATIONS'];
  svgIconService.$inject = [];


  function svgIconDirective(svgIconService, ANIMATIONS){
    var ddo = {
      link: function(scope, elem, attrs){
        attrs.$observe('svgIcon', function(icon){
          var svg = svgIconService.getIcons()[icon];
          if(svg){
            svg = angular.element(svg);
            svg.css('width', attrs.width ? attrs.width + 'px' : '64px');
            var animation = ANIMATIONS[icon.charAt(0)];
            svg.attr('class','animated '+animation);
            elem.replaceWith(svg);
          }
        })
      }
    }
    return ddo;
  }

  function svgIconService(){
    var ICONS = {
      'X-icon': "<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 100 100'><polygon fill='#DB9E36' fill-rule='nonzero' points='100 11.5399943 88.4595969 0 50.0001022 38.4599035 11.5404031 0 0 11.5399943 38.4596991 50.0001022 0 88.4595969 11.5406074 100 50.0001022 61.5403009 88.4595969 100 99.9997956 88.4595969 61.5403009 50.0001022'></polygon></svg>",
      'O-icon': "<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 100 100'><circle stroke='#DC3522' stroke-width='14' fill='transparent' cx='50' cy='50' r='43' /></svg>"
    }
    this.getIcons = function(){
      return ICONS;
    }
  }

})();

;(function(){
  angular.module('triflix')
  .directive('selectOption', ['GameSettings', function(GameSettings){
    /*paint the background of a selected option in the setting panel */
    var ddo = {
      scope: {
        option: '@selectOption',
        value: '=selectOptionValue'
      },
      link: function(scope, elem, attrs){
        if(_.isEqual(GameSettings.getSettings()[scope.option], scope.value))
          elem.addClass('settings__content__el__selected');

        elem.on('click', function(){
          var options = elem.siblings();
          options.removeClass('settings__content__el__selected');
          elem.addClass('settings__content__el__selected');
        })

      }
    }
    return ddo;
  }])

})();

;(function(){
  angular.module('triflix')
  .directive('refreshBoard', ['$rootScope', 'SocketService', function($rootScope, SocketService){
    var ddo = {
      link: function(scope, elem, attrs){
        /*remove all the classes and icons from the board everytime you start the game*/

        var unwatch = $rootScope.$on('triflix.game.start', function(){
          elem.children().removeClass('board__cell--selected board__cell--o board__cell--x');
          angular.forEach(elem.children(), function(child){
            $(child).find('.icon').remove();
          })
        })

        scope.$on('$destroy', function(){
          unwatch();
        })

        if(SocketService.getOpponent()){
          //se multiplayer levo subito le icone di esempio
          $rootScope.$emit('triflix.game.start');
        }
      }
    }
    return ddo;
  }])
})();

;(function(){
  angular.module('triflix')
  .directive('paintCell', ['Game', '$compile', 'ANIMATIONS',
    function(Game, $compile, ANIMATIONS){
    /*paint a single cell on the board*/
    var ddo = {
      scope: {
        cell: '=paintCell',
        game: '='
      },
      link: function(scope, elem, attrs){
        var icon = angular.element('<span class="icon animated {{iconEffect}}"></span>');

        scope.$watch('cell', function(newVal){
          if(!newVal) return;
          scope.iconEffect = ANIMATIONS[scope.cell] || 'bounceIn';
          elem.removeClass('board__cell--x board__cell--o');
          elem.addClass('board__cell--selected board__cell--'+scope.cell.toLowerCase());
          elem.append($compile(icon)(scope));

        })


      }
    }
    return ddo;
  }])



})();

